:global(*),
:global(*::before),
:global(*::after) {
  box-sizing: border-box;
}

:global(*) {
  font-family: inherit;
  margin: 0;
  padding: 0;
}

:global(html) {
  font-family: Inter, sans-serif;
  height: 100%;
  min-height: 100%;
}

:global(body) {
  min-height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-font-smoothing: subpixel-antialiased;
}

:global(ul),
:global(ol) {
  list-style: none;
}

:global(button) {
  background: none;
  border: none;
  color: currentColor;
  font-size: inherit;
  font-weight: inherit;
  outline: none;
}

:global(a) {
  color: inherit;
  font-size: inherit;
  font-weight: inherit;
}

:global(button:hover) {
  cursor: pointer;
}

:global(input:focus) {
  outline: none;
}

:global(h1),
:global(h2),
:global(h3) {
  font-size: inherit;
  font-weight: inherit;
}

:global(:root) {
  --canva-brand: #00c4cc;
  --canva-brand-dark: #00afb5;
  --canva-brand-light: #00d9e1;
  --canva-brand-ultra-light: #bbf3f4;

  --purple: #7d2ae8;
  --purple-ultra-light: #e5ccfd;
  --purple-light: #8d39fa;
  --purple-dark: #6718cf;
  --blue: #19aaee;
  --green: #6bca2c;
  --gold: #fbbe28;

  --mono-black: rgb(12, 19, 24);
  --mono-black-a100: rgba(14, 19, 24, 0.7);
  --mono-black-a150: rgba(14, 19, 24, 0.6);
  --mono-black-a200: rgba(14, 19, 24, 0.45);
  --mono-black-a250: rgba(14, 19, 24, 0.3);
  --mono-black-a275: rgba(14, 19, 24, 0.25);
  --mono-black-a300: rgba(14, 19, 24, 0.2);
  --mono-black-a400: rgba(14, 19, 24, 0.15);
  --mono-black-a500: rgba(14, 19, 24, 0.07);
  --mono-black-a600: rgba(14, 19, 24, 0.025);

  --mono-grey: #293039;
  --mono-grey-light: #edf0f2;
  --mono-grey-ultra-light: #f8f9f9;

  --mono-white: rgb(255, 255, 255);
  --mono-white-a50: rgba(255, 255, 255, 0.95);
  --mono-white-a100: rgba(255, 255, 255, 0.65);
  --mono-white-a200: rgba(255, 255, 255, 0.4);
  --mono-white-a300: rgba(255, 255, 255, 0.15);
  --mono-white-a400: rgba(255, 255, 255, 0.1);
  --mono-white-a500: rgba(255, 255, 255, 0.07);

  --grid-baseline: 0.5rem;
  --border-radius: calc(var(--grid-baseline) * 1);

  --text-normal: 400;
  --text-bold: 600;
}

:global(*),
:global(*::before),
:global(*::after) {
  box-sizing: border-box;
}

:global(*) {
  font-family: inherit;
  margin: 0;
  padding: 0;
}

:global(html) {
  height: 100%;
}

